import { doc, onSnapshot } from "firebase/firestore";
import React, { useContext, useEffect, useState } from "react";
import { AuthContext } from "../context/AuthContext";
import { ChatContext } from "../context/ChatContext";
import { db } from "../firebase";
import Badge from "@mui/material/Badge";

const Chats = () => {
  const [chats, setChats] = useState([]);
  const { currentUser } = useContext(AuthContext);
  const { dispatch } = useContext(ChatContext);

  useEffect(() => {
    const getChats = () => {
      const unsub = onSnapshot(doc(db, "userChats", currentUser.uid), (doc) => {
        setChats(doc.data());
      });

      return () => {
        unsub();
      };
    };
    currentUser.uid && getChats();
  }, [currentUser.uid]);

  const LoggedIn = (props) => {
    const theUser = props.theUser;
    const [status, setStatus] = useState(false);

    const badgeStyle = {
      "& .MuiBadge-badge": {
        backgroundColor: status ? "#00ff2a" : "#ff3333",
        width: 12,
        height: 12,
        right: 5,
        top: -18,
        borderRadius: "50%",
      },
    };
 
    const getObservable = () => {
      const unsub = onSnapshot(doc(db, "users", theUser), (doc) => {
        doc.data() && setStatus(doc.data().loggedin);
        console.log(`${theUser} logged: `, doc.data().loggedin);
      });
      return () => {
        unsub();
      };
    };
    getObservable();
    return (
      <Badge sx={badgeStyle} variant="dot">
        {props.children}
      </Badge>
    );
  };

  const handleSelect = (u) => {
    dispatch({ type: "CHANGE_USER", payload: u });
  };

  return (
    <div className="chats">
      {Object.entries(chats)
        ?.sort((a, b) => b[1].date - a[1].date)
        .map((chat) => (
          <div
            className="userChat"
            key={chat[0]}
            onClick={() => handleSelect(chat[1].userInfo)}
          >
            <img src={chat[1].userInfo.photoURL} alt="" />
            <LoggedIn theUser={chat[1].userInfo.uid} />
            <div className="userChatInfo">
              <span>{chat[1].userInfo.displayName}</span>
              <p>{chat[1].lastMessage?.text}</p>
            </div>
          </div>
        ))}
    </div>
  );
};

export default Chats;
